*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  background: var(--background);
  height: 100vh;
  width: 100vw;
  font-family: "Poppins", sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

img {
  max-width: 100%;
  border-radius: 0.5em;
}

:root {
  --background: #2d2e36;
  --text-header: #fff;
  --text-color: #8d91a6;
}

h1 {
  margin-top: 0;
  font-size: 1.875rem;
  color: var(--text-header);
  text-transform: capitalize;
  font-weight: 400;
}

p {
  color: var(--text-color);
  line-height: 1.5;
}

.container {
  background: #383a45;
  width: 360px;
  height: 615px;
  border-radius: 0.5em;
  padding: 2em;
}